import http from '@/utils/http';
import type { MutationTree, ActionTree, GetterTree } from 'vuex'
import type { State } from '../index'
import AmRequestAddress from "@/providers/am-request-address";

interface Infos {
  [index:string]: unknown
}
export interface UsersState {
  token: string,
  infos: Infos
}

const state: UsersState = {
  token: '',
  infos: {}
};

const mutations: MutationTree<UsersState> = {
  updateToken(state, payload){
    state.token = payload;
  },
  updateInfos(state, payload){
    state.infos = payload;
  },
  clearToken(state){
    state.token = '';
  }
};
const actions: ActionTree<UsersState, State> = {
  login(context, payload){
    return http.post(AmRequestAddress.login, payload)
  },
  infos(){
    return http.get(AmRequestAddress.getUserInfos)
  }
};
const getters: GetterTree<UsersState, State> = {};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}